<template>
  <div class="machine">
    <header>
      <p class="num">100021</p>
      <p>机器编号</p>
    </header>
    <main>
      <p>请选择烘干时长</p>
      <div class="content">
        <ul>
          <li>
            <img src alt />
            <div class="useTime">
              <h1>30分钟</h1>
              <p>建议1~3件衣物时使用</p>
            </div>
            <span>￥10</span>
          </li>
        </ul>
        <input type="radio" />
        <span>
          阅读并同意《
          <span>干衣机使用协议</span>》
        </span>
      </div>
    </main>
    <footer>
      <span>立即支付</span>
      <p>已选择120分钟，需支付34元</p>
    </footer>
  </div>
</template>


<script>
export default {};
</script>

<style lang="scss">
.machine {
  header {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 205px;
    background: url(../../assets/images/time_bg@2x.png) no-repeat;
    background-size: 100% 100%;
    color: #fff;
    .num {
      margin-top: 20px;
      margin-bottom: 8px;
      font-size: 27px;
    }
  }
}
</style>


